    /**
     * 
     */
    parentKeys: string[];
    /**
     * 
     */
    globalOptions: Immutable.Map<string, any>;
    /**
     * ajv
     */
    ajv: Ajv;
    /**
     * 
     */
    arrayIndex?: number;
    /**
     * formItem
     */
